.loader {
  width: 150px;
  height: 150px;
  box-sizing: border-box;
  background: #e54919;
  margin: 20px auto;
  animation: rotationBack 2.5s ease-in-out infinite reverse;
  display: block;
  position: relative;
  top: 180px;
  left: 0;
}

.loader:before {
  content: "";
  box-sizing: border-box;
  width: 150px;
  height: 150px;
  background: #e54919;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(45deg);
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 140px;
  height: 140px;
  background: rgba(226, 128, 0, .565);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 15px rgba(0, 0, 0, .25);
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

